import React,{Component} from 'react'
import {
    CButton,
    CCard,
    CCardBody,
    CCardFooter,
    CCardHeader,
    CCol,
    CForm,
    CFormGroup,
    CInput,
    CLabel,
  } from '@coreui/react'

import API from 'src/api';

import { history } from 'src/history';
 
class AddUser extends Component {
    constructor(props) {
      super(props);      
      this.state = {
        name:'',
        email:'',
        password:'',
        confirmPassword:''
      }  
      this.handleInputChange = this.handleInputChange.bind(this);
      this.submitButton = this.submitButton.bind(this);
    }

    handleInputChange(event) {
      const target = event.target;
      const value = target.type === 'checkbox' ? target.checked : target.value;
      const name = target.name;
  
      this.setState({
        [name]: value
      });
    }

    async submitButton(){ 
      if(this.state.password.length >=8 &&
        (this.state.password === this.state.confirmPassword))
      {
        let body = {
          name:this.state.name,
          email:this.state.email,
          password:this.state.password        
        } 
        await API.post("/users/add", body)
        .then((response)=>{
          console.log(response.code)  
        }).catch((error)=>{
          alert("Try Again");
        });
        this.resetButton();       
      }
      else{
        alert("Please enter valid password")
      }
    }

    resetButton = ()=>{           
        this.setState((prevState) => ({
          name:'',
          email:'',          
          password:'', 
          confirmPassword:'',         
        }),);        
    }
  
    render() {
      let {name,email,password,confirmPassword} = this.state;
      return (
        <>
        <CCol xs="12">
        <CCard>
            <CCardHeader>
              <span className="h4">
                Add User
              </span>
              <div className="btn btn-outline-info float-right mx-2" onClick={()=>{history.goBack()}}>
                Back
              </div>
            </CCardHeader>
            <CCardBody>
              <CForm action="" method="post" encType="multipart/form-data" className="form-horizontal">                
                <CFormGroup row>
                  <CCol md="3">
                    <CLabel htmlFor="name">Name</CLabel>
                  </CCol>
                  <CCol xs="12" md="6">
                    <CInput onChange={this.handleInputChange} id="name" name="name" value={name} placeholder="Enter Name" />                    
                  </CCol>
                </CFormGroup>                
                <CFormGroup row>
                  <CCol md="3">
                    <CLabel htmlFor="email">Email</CLabel>
                  </CCol>
                  <CCol xs="12" md="9">
                    <CInput onChange={this.handleInputChange} type="email" id="email" name="email" placeholder="Enter Email" value={email}/>                    
                  </CCol>
                </CFormGroup>             
                <CFormGroup row>
                  <CCol md="3">
                    <CLabel htmlFor="password">Password</CLabel>
                  </CCol>
                  <CCol xs="12" md="9">
                    <CInput onChange={this.handleInputChange} type="password" id="password" name="password" placeholder="Enter Password" value={password}/>                    
                  </CCol>
                </CFormGroup>             
                <CFormGroup row>
                  <CCol md="3">
                    <CLabel htmlFor="confirmPassword">Confirm Password</CLabel>
                  </CCol>
                  <CCol xs="12" md="9">
                    <CInput onChange={this.handleInputChange} type="password" id="confirmPassword" name="confirmPassword" placeholder="Confirm Password" value={confirmPassword}/>                    
                  </CCol>
                </CFormGroup>             
              </CForm>
            </CCardBody>
            <CCardFooter>
              <CButton type="submit" className="m-1" size="sm" color="primary" onClick={this.submitButton}>Submit</CButton>
              <CButton type="reset" size="sm" className="m-1" color="danger" onClick={this.resetButton}>Reset</CButton>
            </CCardFooter>
          </CCard>
        </CCol>
    </>
      );
    }
}

export default AddUser;